.root {
  display: none;
}

@media (min-width: 600px) {
  .root {
    display: flex;
    flex-direction: column;
    margin-top: 50px;
    margin-bottom: 50px;
  }
  :root {
    --swiper-navigation-color: rgb(255, 255, 255);
    --swiper-pagination-color: rgb(255, 0, 0);
    --swiper-pagination-bullet-bottom: 100px;
    --swiper-pagination-bullet-border-radius: 50px;
    --swiper-pagination-bullet-width: 80px;
    --swiper-pagination-bullet-height: 20px;
    --swiper-pagination-bullet-inactive-color: #ffffff;
    --swiper-pagination-bullet-inactive-opacity: 0.8;
    --swiper-pagination-bullet-opacity: 1;
    --swiper-pagination-bullet-horizontal-gap: 10px;
    --swiper-pagination-bullet-vertical-gap: 16px;
  }
  .swiper {
    height: 500px;
    width: 100%;
    overflow: hidden;
    display: flex;
    flex-direction: row;
  }
  .slide_container {
    position: relative;
    height: 100%;
  }
  .slide_container_text {
    position: absolute;
    top: 100px;
    left: 50px;
    z-index: 2;
    height: 100%;
  }
  .slide_status {
    font-size: 24px;
    font-weight: 900;
    color: #e52d27;
  }
  .slide_header {
    font-size: 24px;
    margin-bottom: 20px;
  }
  .slide_description {
    display: block;
    font-size: 14px;
    width: 100%;
    max-width: 200px;
  }
  .header {
    margin-left: 50px;
    font-size: 32px;
  }

  .image_container {
    display: flex;
    justify-content: center;
    align-items: center;
    padding-left: 100px;
    overflow: hidden;
  }
  .image_container img {
    flex-shrink: 0;
    min-width: 100%;
    min-height: 100%;
  }
}

@media (min-width: 800px) {
  .header {
    font-size: 32px;
    margin-left: 100px;
  }
  .slide_container_text {
    padding-left: 50px;
  }
  .slide_description {
    max-width: 280px;
  }
  .image_container {
    padding-left: 200px;
  }
}

@media (min-width: 1200px) {
  .header {
    font-size: 48px;
    margin-left: 50px;
  }
  .root {
    margin-left: 50px;
    margin-right: 50px;
  }
  .slide_description {
    font-size: 16px;
    max-width: 400px;
  }
}

@media (min-width: 1500px) {
  
  .header {
    font-size: 48px;
  }
}
